/* Nav Section */
.navbar-header {
  display: flex;
  -webkit-box-pack: justify;
  justify-content: space-between;
  -webkit-box-align: center;
  position: fixed;
  background-color: var(--clr-dark);
  top: 0px;
  z-index: 11;
  padding: 0px 0px;
  width: 100%;
  height: 80px;
  filter: none;
  transition: var(--transition);
  pointer-events: auto;
  user-select: auto;
}

.main-nav {
  display: flex;
  -webkit-box-pack: justify;
  -webkit-box-align: center;
  align-items: center;
  justify-content: space-between;
  position: relative;
  width: 100%;
  counter-reset: item 0;
  z-index: 12;
  border-bottom: 2px var(--clr-accent) solid;
}

.nav-links-container {
  display: flex;
  -webkit-box-align: center;
  align-items: center;
}

.nav-links {
  display: flex;
}

.main-nav ul {
  list-style: none;
}

.main-nav a {
  color: inherit;
  text-decoration: none;
  padding: 1rem;
}

.main-nav a:hover {
  color: var(--clr-accent);
}

.nav-logo img {
  position: relative;
  text-decoration: none;
  top: 10px;
  width: 120px;
  height: auto;
  justify-self: center;
}

.burger {
  display: none;
}

.burger div {
  width: 25px;
  height: 2px;
  background-color: var(--clr-accent);
  margin: 5px;
}

.nav-active {
  transform: translateX(0%) !important;
}

@keyframes navLinkFade {
  from {
    opacity: 0;
    transform: translateX(50px);
  }
  to {
    opacity: 1;
    transform: translateX(0px);
  }
}

@media screen and (max-width: 768px) {
  body {
    overflow-x: hidden;
  }

  .nav-links {
    position: absolute;
    right: 0px;
    height: 92vh;
    top: 6.5vh;
    padding: 0%;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: space-around;
    background-color: var(--clr-dark);
    width: 30%;
    transform: translateX(100%);
    transition: transform 0.5s ease-in;
    border-left: 2px var(--clr-accent) solid;
    /* opacity: 0.99; */
  }

  .nav-links li {
    opacity: 0;
  }

  .nav-links li:hover {
    font-weight: var(--fw-bold);
  }

  .burger {
    display: block;
    cursor: pointer;
  }
}

