  flex-grow: 1;
  /* margin-top: 200px; */
  min-height: 100vh;
}

.philip-img {
  display: flex;
  flex-grow: 1;
  /* width: 50%; */
  /* height: auto; */
  align-items: center;
  justify-content: center;
  z-index: 2;
}

