.philip-img img {
  min-width: 300px;
  max-width: 50%;
  height: auto;
  box-shadow: 10px 5px 7px var(--clr-dark);
  z-index: 2;
}

.home .home-text {
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  margin: 5em;
  align-self: center;
}

.home .home-text h1 {
  align-items: flex-end;
}

.home-title {
  position: relative;
  background-color: var(--clr-accent);
  text-align: right;
  width: 175%;
  left: -75%;
  padding: 2rem 0.5rem;
}

.resume {
  text-decoration: none;
  border: var(--clr-accent) 2px solid;
  border-radius: 7px;
  padding: 1rem;
  margin-top: 50px;
  color: var(--clr-accent);
  transition: 0.3s;
}

.resume:hover {
  background-color: rgba(109, 152, 134, 0.1);
}

@media screen and (max-width: 768px) {
  .home {
    display: flex;
    flex-direction: column;
    margin-top: 150px;
  }

  .home-title {
    left: auto;
    width: auto;
    padding: 1.5rem 0.5rem;
    margin-top: 0;
  }
}

/* About section */
.about {
  display: flex;
  flex-direction: column;
  margin-left: 5rem;
  margin-right: 5rem;
}

.about-title::after {
  content: "";
  display: block;
  width: 4em;
  height: 2px;
  /* margin: 0.5em auto 1em; */
  opacity: 0.5;
  background: var(--clr-accent);
}

.about-content-1 {
  display: flex;
}

.about-description {
  flex-grow: 2;
}

.about-image-container {
  display: flex;
  flex-grow: 1;
  /* margin-left: 2rem; */
  justify-content: center;
  align-items: center;
}

.philip-utah-img {
  max-width: 65%;
  min-width: 200px;
  height: auto;
  transform: translateY(5%);
  box-shadow: 6px -3px 10px var(--clr-accent);
}

.philip-utah-img-outline {
  display: inline-block;
  position: relative;
}

.philip-utah-img-outline:before {
  position: absolute;
  top: -5%;
  left: 15%;
  width: 65%;
  min-width: 200px;
  height: 100%;
  border: 4px solid rgba(177, 72, 27, 1);
  content: "";
  box-shadow: 4px -4px 20px var(--clr-accent);
  background-color: var(--clr-lighterDark);
}

.skills {
  display: flex;
  flex-direction: row;
  justify-content: space-evenly;
  align-items: stretch;
  background-color: var(--clr-lighterDark);
  padding: 1rem;
  border-radius: 0.5rem;
  flex-wrap: wrap;
}

.skills-col {
  display: flex;
  flex-direction: column;
  list-style: none !important;
  justify-content: flex-end;
  padding: 0;
  margin: 0;
}

.skill {
  position: relative;
  width: 150px;
  height: 50px;
  text-align: center;
  background-color: #c84b31;
  border: 2px var(--clr-dark) solid;
  font-size: var(--fs-h3);
  font-weight: var(--fw-bold);
  box-shadow: 10px -4px 4px var(--clr-dark);
  color: var(--clr-dark);
}

@media screen and (max-width: 768px) {
  .about-content-1 {
    display: flex;
    flex-direction: column-reverse;
  }

  .about-description {
    margin-bottom: 3rem;
  }

  .about-image-container {
    margin-top: 2rem;
    margin-bottom: 5rem;
  }
}

