  }
}

/* work */
.work {
  display: flex;
  flex-direction: column;
  flex-grow: 1;
  margin-top: 5rem;
}

.work-title {
  display: block;
  text-align: end;
}

.work hr {
  background-color: var(--clr-accent);
  height: 1px;
  border: none;
  width: 75%;
}

.work-item {
  display: flex;
  flex-direction: row;
  margin: 0rem 5rem 5rem;
  padding: 0.5rem;
  background-color: var(--clr-lighterDark);
  box-shadow: inset 3px 3px 3px var(--clr-accent),
    inset -3px -3px 3px var(--clr-accent);
}

.work-item-left {
  display: flex;
  flex-direction: column;
  flex-grow: 1;
  width: 400px;
  margin-left: 0.5rem;
  padding-right: 4rem;
}

.work-item-title {
  margin: 0;
}

.work-item-left a {
  display: flex;
  text-decoration: none;
  color: inherit;
  align-items: center;
}

.git-logo div {
  margin-left: 0.5rem;
}

.work-item-mid {
  display: block;
  flex-grow: 2;
  width: 500px;
}

.project1 {
  background-image: url(./resources/WachItConfigurationPic.png);
  background-repeat: no-repeat;
  background-position: center;
  background-size: 100%;
  opacity: 0.9;
}

.project2 {
  background-image: url(./resources/PeaceOfCake.png);
  background-repeat: no-repeat;
  background-position: center;
  background-size: 100%;
}

.work-item-right {
  display: flex;
  flex-direction: column;
  /* flex-grow: 1; */
  text-align: end;
  margin-left: 2rem;
  width: 100px;
}

.work-item-right ul {
  display: flex;
  flex-direction: column;
  list-style: none;
  margin: 0;
  padding: 0;
  height: 100%;
  align-items: stretch;
  justify-content: space-around;
}

.work-item-right ul li {
  background-color: var(--clr-accent);
  padding: 2px;
  border-radius: 5px;
  width: auto;
}

@media screen and (max-width: 1200px) {
  .work-item {
    margin: 0rem 3rem 3rem;
  }
}

@media screen and (max-width: 768px) {
  .work-item {
    margin: 0 0 3rem;
  }

  .work-item-left {
    padding-right: 0%;
  }

  .work-item-mid {
    display: none;
  }
}

@media screen and (max-width: 500px) {
  .work-item {
    flex-direction: column;
    min-width: 272px;
  }

  .work-item-left {
    width: auto;
  }

  .work-item-right {
    /* width: 80%; */
    text-align: center;
    align-self: center;
    margin: 0.5rem 0.5rem 0;
  }

  .work-item-right ul {
    flex-direction: row;
  }
}

/* Contact */
.contact {
  display: flex;
  flex-direction: column;
  /* flex-grow: 1; */
  max-width: 600px;
  margin: 0px auto 100px;
  justify-content: center;
  align-items: center;
  text-align: center;
}

.email-link {
  text-decoration: none;
  border: var(--clr-accent) 2px solid;
  border-radius: 7px;
  padding: 1rem;
  margin-top: 50px;
  color: var(--clr-accent);
  transition: 0.3s;
}
.email-link:hover {
  background-color: rgba(109, 152, 134, 0.1);
}

/* Footer */
.footer {
  margin: 0 auto 0;
  padding-bottom: 1rem;
  max-width: 600px;
}

.plugs {
  display: flex;
  justify-content: space-around;
  margin-left: 1rem;
  margin-right: 1rem;
}

.creds {
  text-align: center;
  margin-top: 0.5rem;
  margin-bottom: 0;
  color: var(--clr-accent);
}
